.TestList {
  padding: 0px;
  border: 2px solid #00D8FF;
  background: #DDEEFF;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  grid-area: flashcardList;
}

.testCard {
  padding: 20px;
  border: 2px solid #00D8FF;
  font-size: 300%;
  margin: 5px;
  width: 45vw;
  height: 45vh;
}

.testCard span {
  margin: auto;
}

.answerForm {
  margin: 5px;
  width: 500px;
}

.answerForm input {
  height: 50px;
  border: 2px solid black;
  font-size: 150%;
  text-align: center;
}

.accuracy {
  margin: 5px;
  width: 100%;
}



