.StudyList {
  padding: 0px;
  border: 2px solid #00D8FF;
  background: #DDEEFF;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  grid-area: flashcardList;

  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: 90% minmax(30px, 5%);
  grid-gap: 10px;
  grid-template-areas:
    ". center ."
    "next . progress";
  align-items: center;
}

.studyCard {
  padding: 20px;
  border: 2px solid #00D8FF;
  font-size: 300%;
  margin: 5px;
  width: 45vw;
  height: 45vh;
}

.center {
  grid-area: center;
  margin: auto;
}

.next {
  grid-area: next;
}

.progress {
  grid-area: progress;
}

.card {

}



