.Flashcard {
  border: 2px solid black;
  text-align: center;
  color: #333;
  padding: 0px;
  text-align: center;
  -ms-flex-align: center;
      align-items: center;
}

.browseCard {
  color: black;
}

.flip-container {
  -webkit-perspective: 1000;
  -o-perspective: 1000;
  perspective: 1000;

}

	.flip-container:hover .flipper,
  .flip-container.hover .flipper {
		-webkit-transform: rotateY(180deg);
		transform: rotateY(180deg);

	}

.flip-container, .front, .back {

}

.flipper {
	-webkit-transition: 0.6s;
	-webkit-transform-style: preserve-3d;

	-moz-transition: 0.6s;
	-moz-transform-style: preserve-3d;

  -o-transition: 0.6s;
	-o-transform-style: preserve-3d;

	transition: 0.6s;
	transform-style: preserve-3d;

	position: relative;
  height: 100%;

}

.front, .back {
	-webkit-backface-visibility: hidden;
	backface-visibility: hidden;

	position: absolute;
	top: 0;
	left: 0;
}

.front {
  color: white;
	z-index: 2;
  width: 100%;
  height: 100%;
}

.back {
	-webkit-transform: rotateY(180deg);
	transform: rotateY(180deg);

  color: white;
  width: 100%;
	height: 100%;

}

.front .name {
	font-size: 2em;
	display: inline-block;
	background: rgba(33, 33, 33, 0.9);
	color: #f8f8f8;
	font-family: Courier;
	padding: 5px 10px;
	border-radius: 5px;
	bottom: 60px;
	left: 25%;
	position: absolute;
	text-shadow: 0.1em 0.1em 0.05em #333;

	-webkit-transform: rotate(-20deg);
	-ms-transform: rotate(-20deg);
	    transform: rotate(-20deg);
}

.back p {
	position: absolute;
	bottom: 40px;
	left: 0;
	right: 0;
	text-align: center;
	padding: 0 20px;
  font-family: arial;
  line-height: 2em;
}

.studyCard {
  /* background: #66B8Bf; */

  width: 500px;
  height: 300px;
}



