.SetList {
    padding: 20px;
    border: 2px solid #00D8FF;
    background: #DDEEEE;
    height: 90vh;
    overflow: auto;

    grid-area: setList;

    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 10px;
    grid-auto-rows: minmax(100px, auto);
  }



